<template>
  <div class="logg">
    <totalHead></totalHead>
    <div class="logg_form " v-show="logg">
      <div class="login_box">
        <p class="log_deng">用户登录</p>
        <form>
          <p>
            <i class="iconfont icon-zhanghao ic"></i>
            <input type="tel" placeholder="手机号" class="input" v-model="tel" @focus="textF" @blur="change" style="width: 3rem;padding-left: 0.35rem;"  />
            <span style="text-align: left;margin-left: 0.6rem;">{{numMsg}}</span>
          </p>
          <p>
            <i class="iconfont icon-mima ic"></i>
            <input type="password" placeholder="密码" style="width: 3rem;padding-left: 0.35rem;" class="input" v-model="pass" @focus="textF" />
          </p>
          <div class="logg_button">
            <button @click="send" style="width: 3rem;" type="button" @keydown.enter="send" v-if="!load">登录</button>
            <button type="button" style="width: 3rem;" v-if="load">登录 <img src="../../assets/bai.gif" alt="" style="width: 20px;max-height: 100%"></button>
          </div>
        </form>
        <div class="logg_enroll">
          <span @click="roll">没有账号？立即注册</span>
          <span @click="forget">忘记密码？</span>
        </div>
      </div>
    </div>
    <div class="logg_form_form" v-show="register">
      <p class="log_deng">用户注册</p>
      <form>
        <p>
          <i class="iconfont icon-icon_nicheng ic"></i>
          <input type="text" placeholder="昵称" class="input inpu" style="width: 3rem;" v-model.trim="name" @focus="textF" />
        </p>
        <p style="margin-top: 0.2rem;">
           <i class="iconfont icon-1111125 ic"></i>
          <input type="tel" placeholder="手机号" class="input inpu" style="width: 3rem;" v-model.trim="phone"  @focus="textF" @blur="change"/>
        </p>
        <p style="margin-top: 0.2rem;">
           <i class="iconfont icon-mima ic"></i>
          <input type="password" placeholder="密码" class="input inpu" style="width: 3rem;" v-model.trim="pwd" @focus="textF"  />
        </p>
        <p style="margin-top: 10px; margin-left: 0.5rem;">
          <RadioGroup v-model="type">
            <Radio label="0">
              <span style="color: #111111;">普通用户</span>
            </Radio>
            <Radio label="1">
              <span style="color: #111111">代理商</span>
            </Radio>
          </RadioGroup>
        </p>
        <p v-if="type === '1'" style="margin-top: 0.2rem;">
           <i class="iconfont icon-yonghuming ic"></i>
          <input  placeholder="姓名" v-model.trim="agentName" @focus="textF" style="width: 3rem;" class="input inpu" />
        </p>
        <p v-if="type === '1'" style="margin-top: 0.2rem;">
           <i class="iconfont icon-renzheng ic"></i>
          <input  placeholder="身份证号" v-model.trim="agentCard" @focus="textF" style="width: 3rem;" class="input inpu" />
        </p>
        <div class="logg_roll">
          <input type="text" placeholder="验证码" v-model.trim="code" />
          <button type="button" @click="getMessage" v-if="!shoe">{{msg}}</button>
          <button type="button" v-if="shoe">{{time}}s重新获取</button>
        </div>
        <div class="logg_button">
          <button @click="submit" v-if="!show" type="button" @keydown.enter="submit">
         			   立即注册
          </button>
          <button v-if="show" type="button">
            		立即注册
                <img src="../../assets/bai.gif" style="width: 20px;max-height: 100%;margin-left: 10px;"/>
         	</button>
        </div>
      </form>
      <div class="logg_enrolle">
        <span @click="enter">已有账号？立即登录</span>
      </div>
    </div>

  </div>
</template>
<script>
  import totalHead from '../totalHead.vue'
  export default {
    data() {
      return {
        type:"0",
        load: false,
        name: "",
        tel: "",
        phone: "",
        pass: "",
        pwd: "",
        logg: true,
        register: false,
        numMsg: "",
        num: false,
        show: false,
        shoe: false,
        time: 60,
        msg: "手机验证码",
        forShow: false,
        code: '',
        agentName:'',
        agentCode:'',
        agentCard:""
      }
    },
    methods: {
      //		    登录
      send(type) {
        if(this.tel !== "" && this.pass !== "") {
          this.load = true;
          this.post("custom/First/login", {
            username: this.tel,
            password: this.pass
          }).then(res => {
            this.load = false;
            if(res.data.code === 10000) {
              this.$router.push('/establish');
              sessionStorage.setItem("session_key", res.data.data.session_key);
            } else {
              const content = res.data.msg;
              this.$Modal.error({
                title: "提交失败",
                content: content
              });
            }
          })
        } else {
          const content = "帐号/密码不能为空";
          this.$Modal.error({
            title: "登录失败",
            content: content
          });
        }
      },
      //		    注册
      submit() {
        this.show = true;
        if(this.name !== "" && this.phone !== "" && this.pwd !== "" && this.code !== "") {
          this.post('custom/First/register', {
            nickname: this.name,
            username: this.phone,
            password: this.pwd,
            code: this.code,
            is_agency_user: this.type,
            id_cart: this.agentCard,
            agency_realname: this.agentName
          }).then(res => {
            if(res.data.code === 10000) {
              this.show = false;
              this.register = false;
              this.logg = true;
              const content = res.data.msg
              this.$Modal.success({
                title: "注册成功",
                content: content
              });
            } else {
              const content = res.data.msg
              this.$Modal.error({
                title: "注册失败",
                content: content
              });
            }
          })
        } else {
          const content = "帐号/密码不能为空"
          this.$Modal.error({
            title: "注册失败",
            content: content
          });
        }
      },
      roll() {
        this.register = true;
        this.logg = false;
      },
      enter() {
        this.register = false;
        this.logg = true;
      },
      //			手机号验证
      textF() {
        event.target.style.borderColor = 'skyblue';
      },
      change() {
        let myreg = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/;

        if(this.tel && myreg.test(this.tel)) {
          this.num = false;
          this.numMsg = '';
          event.target.style.borderColor = 'gainsboro';
        } else {
          this.num = true;
          this.numMsg = '请输入正确的十一位手机号';
          event.target.style.borderColor = 'red';
        }
      },
      //			手机号验证码获取
      getMessage() {
          console.log(123)
        if(this.phone !== "") {
          const timer = setInterval(() => {
            this.time--;
            if(this.time === 0) {
              this.msg = "重新获取验证码";
              this.show = false;
              this.time = 60;
              clearInterval(timer);
            }
          }, 1000);
          this.post('custom/First/sendMsg', {
            tel: this.phone,
          }).then(res => {
            if(res.data.code === 10000) {
              this.$Modal.success({
                title: "发送成功",
                content: "您的手机号验证码已发送成功请注意查收"
              });
            } else {
              this.$Modal.error({
                title: "发送失败",
                content: "手机号发送失败，请重新发送"
              });
            }
          })
        } else {
          this.$Modal.error({
            title: "获取失败",
            content: "请输入手机号"
          });
        }
      },
      forget() {
        this.$router.push('/forget');
      }
    },
    mounted() {
      if(this.$route.name === "login") {
        this.$store.dispatch("fetch_load", false)
      }
    },

    components: {
      totalHead
    }
  }
</script>

<style>
  .reload {
    transition: 5s transform;
  }

  .reload:hover {
    transform: rotate(720deg);
  }

  .logg {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    background: url("../../assets/login_backgroundImg.png") no-repeat;
    background-size:100% 100%;
    overflow: auto;
    align-content: center;
    flex-direction: column;
  }

  .logg_form,.logg_form_form {
     margin: 2.5rem auto;
     border-radius: 5px;
     width: 4rem;
     background: white;
    box-shadow: 5px 5px 10px #1E87A3;
    padding-bottom: 0.2rem;
  }
  .logg_form_form{
    margin: 1rem auto !important;
    padding-bottom: 0.2rem;
  }
  .login_box{
   position: relative;
  }
  .login_box>form>p{
    text-align: center;
    display: flex;
    flex-direction: column;
    position: relative;
     }
  .login_box>form>p>span{
    height:21px;
    color: red;
    display: inline-block;
  }
  .login_box>form>p>input {
    margin-top: 0.1rem;
    height: 0.4rem;
    width: 4rem;
    border-radius: 0.04rem;
    padding-left: 6px;
    margin: 0.02rem auto;
  }

  .logg_button>button {
    display: block;
    margin: 0 auto;
    cursor: pointer;
    margin-top: 0.3rem;
    color: white;
    background: #96BD13;
    border: none;
    border-radius: 0.04rem;
    height: 0.4rem;
    width: 3rem;
    font-size: 0.15rem;
  }

  .logg_button>button:hover {
    opacity: 0.5;
  }

  .logg_enroll{
    width:4rem;
  }
  .logg_enroll,
  .logg_enrolle {
    margin-top: 0.1rem;
  }
  .logg_enroll>span:nth-child(1) {
    padding-left: 0.5rem;
    color: #2C2C2C;
    font-size: 0.12rem;
  }
.logg_enroll>span:nth-child(2){
   padding-left: 1.3rem;
    color: #2C2C2C;
    font-size: 0.12rem;
}
  .logg_enrolle>span {
    padding-left: 1.2rem;
    color: #2C2C2C;
    cursor: pointer;
    font-size: 0.12rem;
  }

  .logg_enroll>span:hover,
  .logg_roll>button:hover {
    cursor: pointer;
  }

  .logg_roll {
    margin-top: 0.1rem;
    margin-left: 0.5rem;
  }

  .logg_roll>button {
    width: 1rem;
    height: 0.35rem;
    color: white;
    border: none;
    background: #63A35C;
    border-top-right-radius: 0.04rem;
    border-bottom-right-radius: 0.04rem;
  }

  input::-moz-placeholder,
  input:-ms-input-placeholder,
  input::-webkit-input-placeholder {
    color: #666666;
  }

  .logg_roll>input {
    border-top-left-radius: 0.04rem;
    border-bottom-left-radius: 0.04rem;
    width: 1rem;
    height: 0.35rem;
    padding-left: 0.04rem;
    border: 1px solid #E5E5E5;
  }
  .log_deng{
    font-size: 0.18rem;
    color: #5B5b5b;
    font-weight: 550;
    letter-spacing: 0.02rem;
    padding: 0.25rem 0 0.2rem 0.43rem;
  }
  .ic{
    position: absolute;
    left: 0.6rem;
    top: 0.10rem;
    font-size: 0.14rem;
  }
  .inpu{
    display: inline-block;
    margin:0 auto;
  }
  .logg_form>form>p>input,.logg_form_form>form>p>input{
    height: 0.4rem;
    width: 4rem;
    border-radius: 0.04rem;
    padding-left: 32px;
    margin: 0.04rem auto;
    display: block;

  }
  .logg_form_form>form>p{
    position: relative;
  }
</style>



